import React, { Fragment } from "react";
import PropTypes from "prop-types";
import Button from "@material-ui/core/Button";
import { withStyles } from "@material-ui/core/styles";
import Divider from "@material-ui/core/Divider";
import IconButton from "@material-ui/core/IconButton";
import InfoIcon from "@material-ui/icons/Info";
import Tooltip from "@material-ui/core/Tooltip";
import TextField from "@material-ui/core/TextField";
import Switch from "@material-ui/core/Switch";
import FormControlLabel from "@material-ui/core/FormControlLabel";

const engUtils = require("./lib/enigma-utils");
const styles = theme => ({
  container: {
    display: "flex",
    flexWrap: "wrap"
  },
  textField: {
    marginLeft: theme.spacing.unit,
    marginRight: theme.spacing.unit,
    maxWidth: "90%"
  },
  dense: {
    marginTop: 16
  },
  menu: {
    width: 200
  },
  button: {
    margin: theme.spacing.unit
  }
});

class TestatorSecretView extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      open: false,
      showDecryptedSecret: false
    };
    //this.handleCloseEdit = this.handleCloseEdit.bind(this);
  }

  handleClickOpen = () => {
    this.setState({ open: true });
  };

  toggleSecret = () => {
    this.setState(prevState => ({
      showDecryptedSecret: !prevState.showDecryptedSecret
    }));
  };

  displaySecret = secret => {
    // Override if the secret is being interacted with
    // TODO: make this behavior more clear.

    // When editing, the secret has already been decrypted.
    // When making a new secret, it needs to be edited as plaintext.
    if (this.props.actionType === "Edit" || this.props.actionType === "New") {
      return secret;
    }

    // when viewing, the secret must have been saved in an encrypted form, so it
    // may need to be decrypted.
    if (this.props.actionType === "View") {
      if (this.state.showDecryptedSecret) {
        return getDecryptedValueFromHex(secret, this.props.network);
      } else {
        return secret;
      }
    }
  };

  render() {
    const longText = `
                  The secret, which should already be encrypted for the beneficiary with PGP.
              `;

    const { classes } = this.props;

    const actionType = this.props.actionType;
    const shouldDisable = actionType === "View" || this.props.secretViewLoading;

    let secret = {};

    if (actionType === "View") {
      secret = this.props.secretProp;
    } else if (actionType === "Edit" || actionType === "New") {
      secret = this.props.editableSecret;
    }

    const secretUndefinedView = (
      <div style={{ padding: 20 }}>
        <strong>
          No secret selected. Please select an existing secret or add a new one.
        </strong>
      </div>
    );

    if ((secret === undefined || secret === {}) && actionType !== "New") {
      return secretUndefinedView;
    }

    const blankName = secret["name"] === undefined || secret["name"] === "";

    // Necessary because TextField doesn't update if value is undefined.
    const toStr = key => {
      return key === undefined ? "" : key;
    };

    // Necessary because TextField doesn't update if value is undefined.
    const toStrOrZero = key => {
      return key === undefined ? "0" : key;
    };

    const badAddress =
      secret["beneficiary_addr"] === undefined ||
      secret["beneficiary_addr"] === "" ||
      !window.web3.utils.isAddress(secret["beneficiary_addr"]);

    return (
      <div>
        <div style={{ padding: 10, fontSize: "150%" }}>
          {actionType + " Secret"}
        </div>
        <Divider />
        <div>
          <div>
            <strong>{"Secret name: "}</strong>
            <Tooltip
              title={
                "Currently stored unencrypted for both testator and beneficiary."
              }
            >
              <IconButton aria-label="Info">
                <InfoIcon />
              </IconButton>
            </Tooltip>
            <div>
              <TextField
                id="outlined-multiline-static"
                className={classes.textField}
                disabled={shouldDisable}
                onChange={this.props.updateEditable("name")}
                value={toStr(secret["name"])}
                margin="normal"
                variant="outlined"
                helperText={blankName ? "Error: Name cannot be blank" : ""}
                error={blankName}
                fullWidth
              />
            </div>
          </div>
          <br />
          <div>
            <strong>{"Notes for beneficiary: "}</strong>
            <Tooltip
              title={
                "Will only be viewable by the beneficiary and the testator."
              }
            >
              <IconButton aria-label="Info">
                <InfoIcon />
              </IconButton>
            </Tooltip>
          </div>
          <div>
            <TextField
              id="outlined-multiline-static"
              multiline
              rows="4"
              onChange={this.props.updateEditable("beneficiary_notes")}
              value={toStr(secret["beneficiary_notes"])}
              className={classes.textField}
              disabled={shouldDisable}
              margin="normal"
              variant="outlined"
              fullWidth
            />
          </div>
          <div>
            <strong>{"Time Delay: "}</strong>
            <Tooltip
              title={
                "A fixed amount of time from the last check in, before the secret can be viewed by the beneficiary."
              }
            >
              <IconButton aria-label="Info">
                <InfoIcon />
              </IconButton>
            </Tooltip>
          </div>
          <div style={{ paddingBottom: "10px" }}>
            {["days", "hours", "minutes", "seconds"].map(input => {
              return (
                <Fragment key={input}>
                  <TextField
                    id="outlined-multiline-static"
                    value={toStrOrZero(secret.timeBeforeReveal[input])}
                    onChange={this.props.updateTimeEditable(
                      "timeBeforeReveal",
                      input
                    )}
                    className={classes.textField}
                    disabled={shouldDisable}
                    margin="normal"
                    label={input}
                    style={{ width: 100 }}
                    type={"number"}
                    inputProps={{
                      maxLength: 2
                    }}
                  />
                </Fragment>
              );
            })}
          </div>
          <div>
            <Tooltip title={longText}>
              <strong>{"Secret: "}</strong>
            </Tooltip>
            {actionType === "View" && (
              <FormControlLabel
                style={{ paddingLeft: 10 }}
                control={
                  <Switch
                    checked={this.state.showDecryptedSecret}
                    onChange={this.toggleSecret}
                    value="checkedB"
                    color="primary"
                  />
                }
                label={
                  <Tooltip
                    title={"Toggle to show decrypted secret (not recommended)"}
                  >
                    <IconButton aria-label="Info">
                      <InfoIcon />
                    </IconButton>
                  </Tooltip>
                }
              />
            )}
          </div>
          <div style={{ padding: 10 }}>
            <TextField
              id="outlined-multiline-static"
              multiline
              rows="4"
              value={this.displaySecret(toStr(secret.secret))}
              onChange={this.props.updateEditable("secret")}
              className={classes.textField}
              disabled={shouldDisable}
              margin="normal"
              variant="outlined"
              fullWidth
            />
          </div>
          <div>
            <strong>{"Beneficiary ETH address: "}</strong>
            <Tooltip
              title={
                "Will be used to identify the beneficiary, make sure this is correct."
              }
            >
              <IconButton aria-label="Info">
                <InfoIcon />
              </IconButton>
            </Tooltip>
            <div>
              <TextField
                id="outlined-multiline-static"
                value={toStr(secret.beneficiary_addr)}
                onChange={this.props.updateEditable("beneficiary_addr")}
                className={classes.textField}
                disabled={shouldDisable}
                helperText={badAddress ? "Error: Address is not valid" : ""}
                error={badAddress}
                margin="normal"
                variant="outlined"
                fullWidth
              />
            </div>
          </div>
          <Divider />
        </div>
        {actionType !== "View" ? (
          <div>
            <Button
              onClick={this.props.handleSave}
              className={classes.button}
              color="primary"
              variant={"contained"}
              disabled={blankName || this.props.secretViewLoading}
            >
              Save
            </Button>
            <Button
              className={classes.button}
              onClick={this.props.handleCancel}
              color="primary"
              variant={"contained"}
              disabled={this.props.secretViewLoading}
            >
              Cancel
            </Button>
          </div>
        ) : (
          <div />
        )}
      </div>
    );
  }
}

TestatorSecretView.propTypes = {
  classes: PropTypes.object.isRequired
};

// Function to encrypt values (in this case either address or net worth)
function getEncryptedValue(value) {
  let clientPrivKey =
    "853ee410aa4e7840ca8948b8a2f67e9a1c2f4988ff5f4ec7794edf57be421ae5";
  let enclavePubKey =
    "0061d93b5412c0c99c3c7867db13c4e13e51292bd52565d002ecf845bb0cfd8adfa5459173364ea8aff3fe24054cca88581f6c3c5e928097b9d4d47fce12ae47";
  let derivedKey = engUtils.getDerivedKey(enclavePubKey, clientPrivKey);
  let encrypted = engUtils.encryptMessage(derivedKey, value);

  return encrypted;
}

// TODO: this is hacky and hidden.
function getDecryptedValueFromHex(value, network) {
  let ascii = value;
  if (network === "ropsten") {
    ascii = window.web3.utils.toAscii(value);
  }
  return getDecryptedValue(ascii);
}

// Function to encrypt values (in this case either address or net worth)
function getDecryptedValue(value) {
  let clientPrivKey =
    "853ee410aa4e7840ca8948b8a2f67e9a1c2f4988ff5f4ec7794edf57be421ae5";
  let enclavePubKey =
    "0061d93b5412c0c99c3c7867db13c4e13e51292bd52565d002ecf845bb0cfd8adfa5459173364ea8aff3fe24054cca88581f6c3c5e928097b9d4d47fce12ae47";
  let derivedKey = engUtils.getDerivedKey(enclavePubKey, clientPrivKey);
  let decrypted = engUtils.decryptMessage(derivedKey, value);

  return decrypted;
}

export default withStyles(styles)(TestatorSecretView);
