import React, { Fragment } from "react";
import PropTypes from "prop-types";
import { withStyles } from "@material-ui/core/styles";
import Button from "@material-ui/core/Button";
import Menu from "@material-ui/core/Menu";
import ClickAwayListener from "@material-ui/core/ClickAwayListener";
import Grow from "@material-ui/core/Grow";
import Paper from "@material-ui/core/Paper";
import Popper from "@material-ui/core/Popper";
import MenuItem from "@material-ui/core/MenuItem";
import MenuList from "@material-ui/core/MenuList";
import Divider from "@material-ui/core/Divider/Divider";
import ListSubheader from "@material-ui/core/ListSubheader/ListSubheader";
import PersonIcon from "@material-ui/icons/Person";
import MenuIcon from "@material-ui/icons/Menu";
import MediaQuery from "react-responsive";
import Toolbar from "@material-ui/core/Toolbar/Toolbar";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogContentText from "@material-ui/core/DialogContentText";
import DialogTitle from "@material-ui/core/DialogTitle";
import Slide from "@material-ui/core/Slide";

const styles = theme => ({
  root: {
    flexGrow: 1
  },
  grow: {
    flexGrow: 1
  },
  menuButton: {
    marginLeft: -12,
    marginRight: 20
  }
});

function shortAddr(addr) {
  return addr.substring(0, 8);
}

class CustomMenuItem extends React.Component {
  render() {
    return (
      <Fragment>
        <Button
          aria-owns={
            this.props.menuOpen === this.props.menuName
              ? this.props.menuName
              : undefined
          }
          aria-haspopup="true"
          onClick={this.props.handleMenu(this.props.menuName)}
          color="inherit"
        >
          {this.props.renderSelected()}
        </Button>
        <Popper
          open={this.props.menuOpen === this.props.menuName}
          anchorEl={this.props.anchorEl}
          transition
          disablePortal
        >
          {({ TransitionProps, placement }) => (
            <Grow
              {...TransitionProps}
              id={this.props.menuName}
              style={{
                transformOrigin:
                  placement === "bottom" ? "center top" : "center bottom"
              }}
            >
              <Paper>
                <ClickAwayListener onClickAway={this.props.handleClose}>
                  {this.props.renderMenuList}
                </ClickAwayListener>
              </Paper>
            </Grow>
          )}
        </Popper>
      </Fragment>
    );
  }
}

class CustomMenu extends React.Component {
  state = {
    anchorEl: null,
    menuOpen: null,
    menuOpenMobile: null,
    viewDialogOpen: { "dialog-account": false, "dialog-contract": false }
  };

  renderMenuList = (menuName, menuItems, renderFunc) => {
    return (
      <React.Fragment>
        <MenuList>
          {menuItems.map((item, index) => (
            <MenuItem
              key={index}
              onClick={this.handleMenuItemClick(menuName, index)}
              selected={index === this.props.currMenuIndices[menuName]}
            >
              {renderFunc(item)}
            </MenuItem>
          ))}
        </MenuList>
      </React.Fragment>
    );
  };

  renderListedAccount = account => {
    return account["Type"] + ":" + account["Address"] + "...";
  };

  renderListedContract = contract => {
    return contract.name + ":" + contract.address;
  };

  renderListedRole = role => {
    return role;
  };

  renderSelectedContract = () => {
    const index = this.props.currMenuIndices["contract"];
    let val = this.props.allContracts[index];
    return (
      val.prefix +
      " Contract: " +
      val.name +
      " (" +
      shortAddr(val.address) +
      "...)"
    );
  };

  renderSelectedRole = () => {
    const index = this.props.currMenuIndices["role"];
    let val = this.props.allRoles[index];
    return "Role: " + val;
  };

  renderSelectedAccount = () => {
    let val =
      this.props.allAccounts === undefined
        ? [{ Type: "None", Address: "None" }]
        : this.props.allAccounts;
    const index = this.props.currMenuIndices["account"];
    return (
      "Account: " +
      val[index]["Type"] +
      " (" +
      shortAddr(val[index]["Address"]) +
      "...)"
    );
  };

  handleMenuItemClick = (menuName, index) => () => {
    // TODO: is there a better way to pass props up if I want them at each level?
    this.props.setCurrMenuIndex(menuName)(index);
    this.handleClose();
  };

  handleMenu = menuName => event => {
    this.setState({ menuOpen: menuName, anchorEl: event.currentTarget });
  };

  handleMenuMobile = event => {
    this.setState({ menuOpenMobile: true, anchorEl: event.currentTarget });
  };

  handleClose = () => {
    this.setState({ anchorEl: null, menuOpen: null });
  };

  handleCloseMobile = () => {
    this.setState({ anchorEl: null, menuOpenMobile: null });
  };

  handleCloseViewDialog = kind => event => {
    let viewDialogOpenC = JSON.parse(JSON.stringify(this.state.viewDialogOpen));
    viewDialogOpenC[kind] = false;
    this.setState({
      viewDialogOpen: viewDialogOpenC
    });
  };

  handleOpenViewDialog = kind => event => {
    let viewDialogOpenC = JSON.parse(JSON.stringify(this.state.viewDialogOpen));
    viewDialogOpenC[kind] = true;
    this.setState({
      anchorEl: null,
      menuOpenMobile: null,
      viewDialogOpen: viewDialogOpenC
    });
  };

  render() {
    const { anchorEl } = this.state;

    // const displayAccounts = (selected)
    const contracts = this.props.allContracts;

    const { allAccounts } = this.props;
    let accounts =
      allAccounts !== undefined
        ? allAccounts
        : [{ Type: "None", Address: "None" }];

    const roles = this.props.allRoles;

    const renderMenuItemsMobile = () => {
      let roleMenuId = "menu-role";
      let roleMenuName = "role";

      // TODO: Fix the hack here for the dialog info, we're only using the first element
      // for the contract and account
      return (
        <React.Fragment>
          <Button onClick={this.handleMenuMobile}>
            <MenuIcon />
          </Button>
          <Popper
            open={this.state.menuOpenMobile != null}
            anchorEl={anchorEl}
            transition
            disablePortal
            placement={"top-end"}
          >
            {({ TransitionProps, placement }) => (
              <Grow
                {...TransitionProps}
                id={roleMenuId}
                style={{
                  transformOrigin:
                    placement === "bottom" ? "center top" : "center bottom"
                }}
              >
                <Paper>
                  <ClickAwayListener onClickAway={this.handleCloseMobile}>
                    <React.Fragment>
                      <MenuList>
                        <MenuItem
                          onClick={this.handleOpenViewDialog("dialog-account")}
                        >
                          View Account
                        </MenuItem>
                        <MenuItem
                          onClick={this.handleOpenViewDialog("dialog-contract")}
                        >
                          View Contract
                        </MenuItem>
                      </MenuList>
                      <Divider />
                      <ListSubheader>Role</ListSubheader>
                      <MenuList>
                        {this.props.allRoles.map((item, index) => (
                          <MenuItem
                            key={index}
                            onClick={this.handleMenuItemClick(
                              roleMenuName,
                              index
                            )}
                            selected={
                              index === this.props.currMenuIndices["role"]
                            }
                          >
                            {item}
                          </MenuItem>
                        ))}
                      </MenuList>
                    </React.Fragment>
                  </ClickAwayListener>
                </Paper>
              </Grow>
            )}
          </Popper>
          <ViewInfoDialog
            viewDialogOpen={this.state.viewDialogOpen["dialog-account"]}
            handleCloseViewDialog={this.handleCloseViewDialog("dialog-account")}
            title={"Account Info"}
            body={
              <div>
                Type: {allAccounts[0].Type}
                <br />
                Address: {allAccounts[0].Address}
              </div>
            }
          />
          <ViewInfoDialog
            viewDialogOpen={this.state.viewDialogOpen["dialog-contract"]}
            handleCloseViewDialog={this.handleCloseViewDialog(
              "dialog-contract"
            )}
            title={"Contract Info"}
            body={
              <div>
                Type: {contracts[0].prefix}
                <br />
                Name: {contracts[0].name}
                <br />
                Address: {contracts[0].address}
              </div>
            }
          />
        </React.Fragment>
      );
    };

    const renderMenuItems = () => {
      return (
        <Fragment>
          <CustomMenuItem
            anchorEl={this.state.anchorEl}
            menuOpen={this.state.menuOpen}
            menuName={"menu-contract"}
            handleMenu={this.handleMenu}
            renderSelected={this.renderSelectedContract}
            handleClose={this.handleClose}
            currAccountIndex={this.props.currMenuIndices["contract"]}
            renderMenuList={this.renderMenuList(
              "contract",
              contracts,
              this.renderListedContract
            )}
          />
          <CustomMenuItem
            anchorEl={this.state.anchorEl}
            menuOpen={this.state.menuOpen}
            menuName={"menu-account"}
            handleMenu={this.handleMenu}
            renderSelected={this.renderSelectedAccount}
            handleClose={this.handleClose}
            currAccountIndex={this.props.currMenuIndices["account"]}
            renderMenuList={this.renderMenuList(
              "account",
              accounts,
              this.renderListedAccount
            )}
          />
          <CustomMenuItem
            anchorEl={this.state.anchorEl}
            menuOpen={this.state.menuOpen}
            menuName={"menu-role"}
            handleMenu={this.handleMenu}
            renderSelected={this.renderSelectedRole}
            handleClose={this.handleClose}
            currAccountIndex={this.props.currMenuIndices["role"]}
            renderMenuList={this.renderMenuList(
              "role",
              roles,
              this.renderListedRole
            )}
          />
        </Fragment>
      );
    };

    return (
      <React.Fragment>
        <MediaQuery query="(max-width: 1224px), (max-device-width: 1224px)">
          {renderMenuItemsMobile()}
        </MediaQuery>
        <MediaQuery query="(min-device-width: 1224px) and (min-width: 1224px)">
          {renderMenuItems()}
        </MediaQuery>
      </React.Fragment>
    );
  }
}

function Transition(props) {
  return <Slide direction="up" {...props} />;
}

// Only one of these should be open at a time
// (for viewing the contract or account information).
class ViewInfoDialog extends React.Component {
  render() {
    return (
      <Fragment>
        <Dialog
          open={this.props.viewDialogOpen}
          TransitionComponent={Transition}
          keepMounted
          onClose={this.props.handleCloseViewDialog}
          aria-labelledby="view-dialog-slide-title"
          aria-describedby="view-dialog-slide-description"
        >
          <DialogTitle id="view-dialog-slide-title">
            {this.props.title}
          </DialogTitle>
          <Divider />
          <DialogContent>{this.props.body}</DialogContent>
          <DialogActions>
            <Button onClick={this.props.handleCloseViewDialog} color="primary">
              Close
            </Button>
          </DialogActions>
        </Dialog>
      </Fragment>
    );
  }
}

CustomMenu.propTypes = {
  classes: PropTypes.object.isRequired
};

export default withStyles(styles)(CustomMenu);
