  getLastCheckIn(account, msg) {
    let result;
    if (this.state.network === "private") {
      result = OasisSecretKeeperContract.methods
        .getLastCheckIn()
        .call({ from: account.Address, gas: GAS });
    } else if (this.state.network === "ropsten") {
      result = this.state.secretKeeperContract.getLastCheckIn({
        from: account.Address,
        gas: GAS
      });
    }
    result.then(result => {
      console.log("Got last check in result: " + JSON.stringify(result));
      // TODO: error check here, of course.
      let ts = new Date(parseInt(result) * 1000);
      this.setState({
        lastCheckIn: ts.toLocaleString(),
        checkInLoading: false,
        checkInErrMsg: msg
      });
    });
  }

  async doCheckIn() {
    let account = this.state.allAccounts[0];
    this.setState({ checkInLoading: true, checkInErrMsg: "Checking in..." });

    let result;
    if (this.state.network === "private") {
      // TODO: switch this to use state.
      result = OasisSecretKeeperContract.methods
        .checkIn()
        .send({ from: account.Address, gas: GAS });
    } else if (this.state.network === "ropsten") {
      result = this.state.secretKeeperContract.checkIn({
        from: account.Address,
        gas: GAS
      });
    }
    result
      .then(result => {
        console.log("Got check in receipt: " + JSON.stringify(result));
        this.getLastCheckIn(account, "Check in complete!");
      })
      .catch(err => {
        console.log("Error when checking in: " + err.message);
        this.setState({
          checkInLoading: false,
          checkInErrMsg: err.message
        });
      });
  }

