  async getSecretsForBeneficiaryOasis() {
    const account = this.state.allAccounts[0];
    let staticSecrets = [];
    let numSecrets = 0;

    // First get the number of secrets.
    try {
      numSecrets = await OasisSecretKeeperContract.methods
        .getNumSecretsForBeneficiary()
        .call({
          from: account.Address,
          gas: GAS
        });
    } catch (err) {
      console.log(
        "Error for getting number of secrets from Oasis contract (beneficiary): " +
          err
      );
      this.setState({ checkInErrMsg: err.message });
    }

    // Set up calls to get information for each secret.
    const genPromisesArr = () => {
      let allPromises = [];
      for (let i = 0; i < numSecrets; i++) {
        allPromises.push(
          OasisSecretKeeperContract.methods
            .getSecretForBeneficiary(i)
            .call({ from: account.Address, gas: GAS })
        );
      }
      return allPromises;
    };
    let promises = genPromisesArr();
    try {
      await Promise.all(promises)
        .then(response => {
          for (let i = 0; i < numSecrets; i++) {
            let result = response[i];
            staticSecrets.push({
              name: result.name,
              testator_notes: "NONE",
              beneficiary_notes: result.beneficiaryNote,
              // TODO: it's confusing to convert timeBeforeReveal but not lastCheckIn
              timeBeforeReveal: this.convertMS(result.timeBeforeReveal),
              lastCheckIn: result.lastCheckIn,
              secret: result.secret,
              owner_addr: result.owner
            });

            this.setState({
              secretViewErrMsg: "Successfully retrieved secrets.",
              allSecrets: staticSecrets
            });
          }
        })
        .catch(err => {
          console.log("Error populating secrets (beneficiary): " + err);
          this.setState({ secretViewErrMsg: err.message });
        });
    } catch (err) {
      console.log("Error for getting secret by indices (beneficiary): " + err);
      this.setState({ secretViewErrMsg: err.message });
    }
  }

  async getSecretsForBeneficiary(instance) {
    // TODO: FIX THE HACK.
    if (this.state.network === "private") {
      return this.getSecretsForBeneficiaryOasis();
    }

    const account = this.state.allAccounts[0];

    instance
      .getSecretsForBeneficiary({
        from: account.Address,
        gas: GAS
      })
      .then(result => {
        console.log(
          "Result for secrets (beneficiary): " + JSON.stringify(result)
        );
        var staticSecrets = [];
        var secrets = result.secrets;
        var names = result.names;
        var timeBeforeReveals = result.timeBeforeReveals;
        var lastCheckIns = result.lastCheckIns;
        var owners = result.owners;
        var beneficiaryNotes = result.beneficiaryNotes;

        for (var i = 0; i < secrets.length; i++) {
          staticSecrets.push({
            name: names[i],
            testator_notes: "NONE",
            beneficiary_notes: beneficiaryNotes[i],
            timeBeforeReveal: this.convertMS(timeBeforeReveals[i]),
            lastCheckIn: lastCheckIns[i],
            secret: secrets[i],
            owner_addr: owners[i]
          });
        }
        // TODO: maybe use different allSecrets?
        this.setState({
          allSecrets: staticSecrets,
          // TODO: this is a little hacky and hidden
          currSecretIndex: undefined
        });
      })
      .catch(err => {
        console.log(
          "Error for getting secrets (beneficiary): " + JSON.stringify(err)
        );
        this.setState({ checkInErrMsg: err.message });
      });
  }

