import React from "react";
import classNames from "classnames";
import PropTypes from "prop-types";
import AppBar from "@material-ui/core/AppBar";
import Button from "@material-ui/core/Button";
import CssBaseline from "@material-ui/core/CssBaseline";
import Grid from "@material-ui/core/Grid";
import Toolbar from "@material-ui/core/Toolbar";
import Typography from "@material-ui/core/Typography";
import { withStyles } from "@material-ui/core/styles";
import { Link } from "react-router-dom";
import LockIcon from "@material-ui/icons/Lock";
import HomeContent from "./HomeContent";
import GuideContentFunc from "./GuideContent";
import FAQContentFunc from "./FAQContent";
import { Route } from "react-router-dom";
import { Switch } from "react-router";
import MenuIcon from "@material-ui/icons/Menu";
import MenuItem from "@material-ui/core/MenuItem";
import Menu from "@material-ui/core/Menu";

// TODO: switch back to this once it's stable.
import { unstable_useMediaQuery as useMediaQuery } from "@material-ui/core/useMediaQuery";
import MediaQuery from "react-responsive";

const styles = theme => ({
  "@global": {
    body: {
      backgroundColor: theme.palette.common.white
    }
  },
  appBar: {
    position: "relative",
    width: "100%"
  },
  toolbarTitle: {
    flex: 1
  },
  layout: {
    width: "auto",
    marginLeft: theme.spacing.unit * 3,
    marginRight: theme.spacing.unit * 3,
    [theme.breakpoints.up(900 + theme.spacing.unit * 3 * 2)]: {
      width: 900,
      marginLeft: "auto",
      marginRight: "auto"
    }
  },
  heroContent: {
    maxWidth: 800,
    margin: "0 auto",
    padding: `${theme.spacing.unit * 8}px 0 ${theme.spacing.unit * 6}px`
  },
  heroButtons: {
    marginTop: theme.spacing.unit * 4
  },
  footer: {
    marginTop: theme.spacing.unit * 8,
    borderTop: `1px solid ${theme.palette.divider}`,
    padding: `${theme.spacing.unit * 6}px 0`
  },
  icon: {
    marginRight: theme.spacing.unit * 2
  }
});

const footers = [
  {
    title: "Company",
    description: [
      "About",
      <a
        href={"https://secretkeeperapp.wordpress.com/contact/"}
        style={{ color: "inherit" }}
      >
        Contact
      </a>
    ]
  },
  {
    title: "Features",
    description: ["Time delay", "Secret splitting"]
  },
  {
    title: "Resources",
    description: [
      <Link to={"/guide"} style={{ color: "inherit" }}>
        Guide
      </Link>,
      <Link to={"/faq"} style={{ color: "inherit" }}>
        FAQ
      </Link>,
      <a
        href="https://github.com/secretkeeperapp/secret-keeper-site"
        style={{ color: "inherit" }}
      >
        Github repo
      </a>
    ]
  },
  {
    title: "Legal",
    description: ["Disclaimer", "Terms of use"]
  }
];

class Homepage extends React.Component {
  state = {
    anchorEl: null
  };

  handleMenuIconClick = event => {
    this.setState({ anchorEl: event.currentTarget });
  };

  handleMenuIconClose = event => {
    this.setState({ anchorEl: null });
  };

  render() {
    const { classes } = this.props;
    // This means the user is on a mobile device.
    const { anchorEl } = this.state;
    //const displayMenuIcon = useMediaQuery("(max-device-width: 1224px)");

    const renderMenuIconMenu = () => {
      return (
        <React.Fragment>
          <Button onClick={this.handleMenuIconClick}>
            <MenuIcon
              aria-owns={anchorEl ? "simple-menu" : undefined}
              aria-haspopup="true"
            />
          </Button>
          <Menu
            id="simple-menu"
            anchorEl={anchorEl}
            open={Boolean(anchorEl)}
            onClose={this.handleMenuIconClose}
          >
            <MenuItem onClick={this.handleMenuIconClose}>
              <Link
                to={"/"}
                style={{ textDecoration: "none", color: "inherit" }}
              >
                Home
              </Link>
            </MenuItem>
            <MenuItem onClick={this.handleMenuIconClose}>
              <Link
                to={"/guide"}
                style={{ textDecoration: "none", color: "inherit" }}
              >
                Guide
              </Link>
            </MenuItem>
            <MenuItem onClick={this.handleMenuIconClose}>
              <a
                href={"http://www.secretkeeperapp.wordpress.com"}
                style={{ textDecoration: "none", color: "inherit" }}
              >
                Blog
              </a>
            </MenuItem>
            <MenuItem>
              <Link
                to={"/faq"}
                style={{ textDecoration: "none", color: "inherit" }}
              >
                FAQ
              </Link>
            </MenuItem>
            <MenuItem>
              <Link
                to={"/app"}
                style={{ color: "blue", textDecoration: "none" }}
              >
                App
              </Link>
            </MenuItem>
          </Menu>
        </React.Fragment>
      );
    };

    const renderRegularMenu = () => {
      return (
        <React.Fragment>
          <Link to={"/"} style={{ textDecoration: "none", color: "inherit" }}>
            <Button>Home</Button>
          </Link>
          <Link
            to={"/guide"}
            style={{ textDecoration: "none", color: "inherit" }}
          >
            <Button>Guide</Button>
          </Link>
          <a
            href={"http://www.secretkeeperapp.wordpress.com"}
            style={{ textDecoration: "none", color: "inherit" }}
          >
            <Button>Blog</Button>
          </a>
          <Link to={"/faq"} style={{ textDecoration: "none" }}>
            <Button>FAQ</Button>
          </Link>
          <Link to={"/app"} style={{ textDecoration: "none" }}>
            <Button color="primary" variant="outlined">
              App
            </Button>
          </Link>
        </React.Fragment>
      );
    };

    // See media query examples at https://github.com/contra/react-responsive
    return (
      <React.Fragment>
        <CssBaseline />
        <AppBar position="static" color="default" className={classes.appBar}>
          <Toolbar>
            <LockIcon className={classes.icon} />
            <Typography
              variant="h6"
              color="inherit"
              noWrap
              className={classes.toolbarTitle}
            >
              <Link
                to={"/"}
                style={{ textDecoration: "none", color: "inherit" }}
              >
                SecretKeeper
              </Link>
            </Typography>

            <MediaQuery query="(max-width: 1224px), (max-device-width: 1224px)">
              {renderMenuIconMenu()}
            </MediaQuery>
            <MediaQuery query="(min-device-width: 1224px) and (min-width: 1224px)">
              {renderRegularMenu()}
            </MediaQuery>
          </Toolbar>
        </AppBar>
        <Switch>
          <Route exact path="/" component={HomeContent(this.props)} />
          <Route path="/guide" component={GuideContentFunc(this.props)} />
          <Route path="/faq" component={FAQContentFunc(this.props)} />
        </Switch>
        {/* Footer */}
        <footer className={classNames(classes.footer, classes.layout)}>
          <Grid container spacing={32} justify="space-evenly">
            {footers.map((footer, ind) => (
              <Grid item xs key={ind}>
                <Typography variant="h6" color="textPrimary" gutterBottom>
                  {footer.title}
                </Typography>
                {footer.description.map((item, ind) => (
                  <Typography
                    key={ind}
                    variant="subtitle1"
                    color="textSecondary"
                  >
                    {item}
                  </Typography>
                ))}
              </Grid>
            ))}
          </Grid>
        </footer>
        {/* End footer */}
        <Typography
          variant="h6"
          color="inherit"
          align={"center"}
          noWrap
          style={{ paddingTop: "50px" }}
          className={classes.toolbarTitle}
        >
          Copyright © 2019 SecretKeeper.
          <MediaQuery query={"(max-width: 1224px)"}>
            <br />
          </MediaQuery>
          All rights reserved.
        </Typography>
      </React.Fragment>
    );
  }
}

Homepage.propTypes = {
  classes: PropTypes.object.isRequired
};

export default withStyles(styles)(Homepage);
